<template>
    <d2-container>
        <!-- 角色同步 -->
        <el-row>
            <el-col :span="24" v-if="uid_boo==false">
                <el-alert :title="'对名称为：-- '+ this.$route.query.name +' -- 的角色进行角色同步。'" type="info" :closable="false">
                </el-alert>
            </el-col>
        </el-row>
        <el-row v-if="uid_boo==true">
            <el-col :span="24">
                <el-alert title="您好像还没有选择角色，暂无法进行下一步！" type="error" :closable="false">
                </el-alert>
            </el-col>
        </el-row>
        <!-- </el-table> -->
        <p style="text-align: center; margin: 20px 0 20px">角色同步</p>
        <div style="text-align: center">
            <el-transfer style="text-align: left; display: inline-block" v-model="value3" :render-content="renderFunc.val" :titles="['所有用户', '角色用户']"
                :button-texts="['到左边', '到右边']" :format="{
                  noChecked: '${total}',
                  hasChecked: '${checked}/${total}'
                }" @change="handleChange" :data="tableData">
            </el-transfer>
        </div>

        <el-row>
            <el-col :span="24" style="text-align: center;">
                <el-button type="primary" style="margin-top:30px;" plain @click="submit">保存 </el-button>
                <el-button style="margin-top:30px;" plain @click="clear"> 取消 </el-button>
            </el-col>
        </el-row>
    </d2-container>
</template>

<script>
    import { permissions, put_users,list } from '../../plugin/axios/api.js';
    import { Myerror } from '../../plugin/axios/er.js';
    import qs from 'qs';
    export default {
        name: 'synchronous',
        data() {
            return {
                tableData: [],
                currentRow: null,
                checkedCities1: [],
                cities: [],
                value3: [],
                renderFunc(h, option) {
                    return { val: `<span>${option.key} - ${option.label}</span>` };
                },
                uid_boo: false
            }
        },
        methods: {
            setCurrent(row) {
                this.$refs.singleTable.setCurrentRow(row);
            },
            handleCurrentChange(val) {
                this.currentRow = val;
            },
            permissionsmy() {
                list().then(res => {
                    console.log(res.data);
                    let arr = [];
                    res.data.forEach(ele => {
                        arr.push({
                            key: ele.id,
                            label: ele.name,
                        })
                    })
                    this.tableData = arr;
                })
            },
            handleChange(value, direction, movedKeys) {
                console.log(value, direction, movedKeys);
            },
            submit() {//覆盖保存
                if (this.uid_boo == true) {
                    this.$message({
                        message: '请选择同步用户的角色！',
                        center: true,
                        type: 'error'
                    });
                    return
                }
                // console.log(this.value3);
                let arr = qs.stringify({
                    users: this.value3
                })
                // let permissions = [this.value3];
                let id = this.$route.query.id;
                put_users(arr, id).then(res => {
                    console.log(res);
                    if (res.code) {
                        this.$message({
                            message: res.message,
                            center: true,
                            type: 'success'
                        });
                        return
                    }
                    Myerror(res, this)
                })
            },
            clear(){
                this.value3 = [];
                this.$router.go(-1);
            }
        },
        mounted() {
            if (!this.$route.query.id) {
                this.uid_boo = true;
            }
            this.permissionsmy()
        },
        watch: {

        },
        computed: {

        }
    }
</script>
<style lang="scss">
</style>